import React, { useState } from "react";
import RectangleTabWrapper from "../../../../../optimized/Ui/Tab/Rectangle/RectangleTabWrapper";
import RectangleTabCard from "../../../../../optimized/Ui/Tab/Rectangle/RectangleTabCard";
import StudentAdmissionFees from "../../../../../optimized/UserStaffMember/StudentAdmissionFees/StudentAdmissionFees";
import StudentAllFeesDataInternal from "./StudentAllFeesDataInternal";
import { useLocation } from "react-router-dom";
import StudentAllTotalAdmissionFees from "../../../../../optimized/UserStaffMember/StudentAdmissionFees/StudentAllTotalAdmissionFees";
import StudentOtherFees from "../../../../../optimized/UserStudentMember/StudentProfile/StudentOtherFees/StudentOtherFees";

const StudentAllFeesConfig = ({ isEditable }) => {
  const getQuery = useLocation();
  const [step, setStep] = useState(1);

  // console.info("getQuery", getQuery.state);
  return (
    <>
      <RectangleTabWrapper>
        <RectangleTabCard
          label="admission_fees"
          onClick={() => setStep(1)}
          isActive={step === 1 ? true : false}
        />

        <RectangleTabCard
          label="department_fees"
          onClick={() => setStep(2)}
          isActive={step === 2 ? true : false}
        />
        <RectangleTabCard
          label="other_fees"
          onClick={() => setStep(3)}
          isActive={step === 3 ? true : false}
        />
      </RectangleTabWrapper>

      {step === 1 ? (
        <>
          <div
            style={{
              marginTop: "0.7rem",
            }}
          >
            <StudentAllTotalAdmissionFees accessAs="student" />
          </div>

          <StudentAdmissionFees isEditable={isEditable} />
        </>
      ) : step === 2 ? (
        <StudentAllFeesDataInternal
          sid={getQuery.state?.sid}
          viewAs={getQuery.state?.viewAs}
        />
      ) : step === 3 ? (
        <StudentOtherFees
          sid={getQuery.state?.sid}
          viewAs={getQuery.state?.viewAs}
        />
      ) : null}
    </>
  );
};

export default StudentAllFeesConfig;
